 @use '../../../../Styles/colors' as col;

 .home-content-side-component {
     width: 40%;
     height: 65vh;
     position: relative;
     display: flex;
     justify-content: center;
     align-items: center;

     .home-content-side-component-wrapper {
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         justify-content: space-between;
         width: 70%;
         height: 65%;

         .header {
             font-size: 2rem;
         }

         .body {
             font-size: 1rem;
             line-height: 30px;
             color: col.$Dark-Gray;
         }

         .shop-now-btn {
             font-size: 1rem;
             font-family: 'League Spartan', sans-serif;
             letter-spacing: 10px;
             border: none;
             background: none;
             cursor: pointer;

             .arrow-icon {
                 width: 60px;
             }

             &:hover {
                 opacity: 0.6;
             }
         }
     }

     .aside-buttons-component {
         position: absolute;
         bottom: 0;
         left: 0;

         .aside-buttons {
             width: 50px;
             height: 50px;
             background-color: black;
             color: col.$Very-Dark-Gray;
             border: none;
             font-size: 1.7rem;
         }
     }


     .home-img-side-component {
         width: 60%;
         height: 100%;
         object-fit: cover;
     }
 }